export default function (Vue) {
    //
  Vue.filter('hanhua',function (num) {
        switch (num){
          case 1:return '';
          case 2:return '';
          case 3:return '';
          case 4:return '';
          case 5:return '';
          case 6:return '';
          case 0:return '';
        }
  });
  //
  Vue.filter('weekChange',function (num) {
    num = Number(num);
    switch (num){
      case 1:return '';
      case 2:return '';
      case 3:return '';
      case 4:return '';
      case 5:return '';
      case 6:return '';
      case 0:return '';
    }
  });
  Vue.filter('percent',function (num) {
     return `${num}%`
  })
  Vue.filter('ToFixed2',function (num) {
      if(!!num){
        return Number(num).toFixed(2);
      }else {
        return num;
      }
  })
  Vue.filter('line',function (str) {
    return str.replace('-','/').replace('-','/')
  })
  Vue.filter('TransferNum',function (str) {
     if(str==null){
       return ''
     }else{
       return Number(str)
     }
  })
}



